import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { delExpense, edition } from '../actions';

class TableWallet extends React.Component {
  renderTitleTable() {
    return (
      <tr className="tr-title">
        <th>Description</th>
        <th>Tag</th>
        <th>Method payment</th>
        <th>Value</th>
        <th>Currency</th>
        <th>Exchange</th>
        <th>Converted value</th>
        <th>Conversion</th>
        <th>Edit/Delete</th>
      </tr>
    );
  }

  renderExpense(expense) {
    const { description, tag, method, value, currency, exchangeRates, id } = expense;
    const { del, edit } = this.props;
    return (
      <tr className="tr-expense" key={ id }>
        <td>{description}</td>
        <td>{tag}</td>
        <td>{method}</td>
        <td>{Number(value).toFixed(2)}</td>
        <td>{exchangeRates[currency].name}</td>
        <td>{Number(exchangeRates[currency].ask).toFixed(2)}</td>
        <td>{Number(value * exchangeRates[currency].ask).toFixed(2)}</td>
        <td>Real</td>
        <td>
          <button
            onClick={ () => edit(id, true) }
            className="btn-edit"
            type="button"
            data-testid="edit-btn"
          >
            E
          </button>
          <button
            onClick={ () => del(id) }
            className="btn-del"
            type="button"
            data-testid="delete-btn"
          >
            X
          </button>
        </td>
      </tr>
    );
  }

  renderLinesTable() {
    const { listExpenses } = this.props;
    return listExpenses.map((expense) => this.renderExpense(expense));
  }

  render() {
    return (
      <table className="container-table">
        { this.renderTitleTable() }
        { this.renderLinesTable() }
      </table>
    );
  }
}

const mapStateToProps = (state) => ({
  listExpenses: state.wallet.expenses,
});

const mapDispatchToProps = (dispatch) => ({
  del: (value) => dispatch(delExpense(value)),
  edit: (id, status) => dispatch(edition(id, status)),
});

TableWallet.propTypes = {
  listExpenses: PropTypes.arrayOf(PropTypes.object).isRequired,
  del: PropTypes.func.isRequired,
  edit: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(TableWallet);
