import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { saveEmail } from '../actions';
import '../styles/login.css';
import { users, addUser } from '../users';

class Login extends React.Component {
  constructor(props) {
    super(props);

    this.validEmail = this.validEmail.bind(this);
    this.validPassword = this.validPassword.bind(this);
    this.updateState = this.updateState.bind(this);
    this.updateStatus = this.updateStatus.bind(this);

    this.state = {
      email: '',
      password: '',
      status: true,
    };
  }

  componentDidUpdate() {
    const { email, password, status } = this.state;
    if (status) {
      if (this.validEmail(email) && this.validPassword(password)) {
        return this.updateStatus(false);
      }
    } else if (!this.validEmail(email) || !this.validPassword(password)) {
      return this.updateStatus(true);
    }
  }

  updateStatus(bool) {
    this.setState((state) => ({ ...state, status: bool }));
  }

  validEmail(email) {
    const one = 1;
    const five = 5;
    let user = '';
    let final = '';
    if (email.includes('@') && email.includes('.com')) {
      user = (email.slice(0, email.indexOf('@')));
      final = (email.slice(email.indexOf('@') + one));
    }
    if (user.length >= one && final.length >= five) return true;
    return false;
  }

  validPassword(password) {
    const six = 6;
    if (password.length >= six) return true;
    return false;
  }

  updateState(event) {
    const { name, value } = event.target;
    this.setState((state) => ({ ...state, [name]: value }));
  }

  handleClickGo(type) {
    const { email, password } = this.state;
    const { saveMail } = this.props;
    if (type === 'enter') {
      if (users.some((user) => user.email === email && user.password === password)) {
        saveMail(email);
      } else alert('Email or Password invalid !');  
    } else {
      addUser({ email, password });
      saveMail(email);
    }
  }

  forgotPassword() {
    const { email } = this.state;
    if (this.validEmail(email)) alert(`Password sent for ${ email }`);
    else alert('Insert email valid');
  }

  renderBtns() {
    const { status } = this.state;
    return (
      <div className="container-btns-login">
        <Link to="/wallet">
          <button
            className="btn-enter"
            disabled={ status }
            onClick={ () => this.handleClickGo('enter') }
            type="button"
          >
            Enter
          </button>
        </Link>
        <Link to="/wallet">
          <button
            type="button"
            className="btn-enter"
            disabled={ status }
            onClick={ () => this.handleClickGo('register') }
          >
            Register
          </button>
        </Link>
      </div>
    );
  }

  render() {
    return (
      <div className="container-center">
        <div className="container-login">
          <h1>Login</h1>
          <input
            name="email"
            type="text"
            data-testid="email-input"
            placeholder="Email"
            onChange={ this.updateState }
          />
          <input
            name="password"
            type="password"
            data-testid="password-input"
            placeholder="Password"
            onChange={ this.updateState }
          />
          { this.renderBtns() }
          <button
            type="button"
            onClick={ () => this.forgotPassword() }
            className="btn-forgot"
          >
            Forgot password ?
          </button>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => ({
  saveMail: (value) => dispatch(saveEmail(value)),
});

Login.propTypes = {
  saveMail: PropTypes.func.isRequired,
};

export default connect(null, mapDispatchToProps)(Login);
